footer {
  background-color: #82a6cb;
  flex-grow: 3;
  grid-template-columns: repeat(9, 1fr) 2fr;
  gap: 10px;
  width: 100%;
  min-width: 700px;
  min-height: 50px;
  max-height: 50px;
  display: grid;
  position: fixed;
  bottom: 0;
}

footer.dark-mode {
  background-color: #1a1a2e;
}

.contacto.dark-mode {
  color: #e0e0e0;
}

.contacto {
  grid-column: 10;
  justify-content: end;
  justify-items: center;
}

header {
  background-color: #82a6cb;
  grid-template-columns: 2fr repeat(8, 1fr) 2fr;
  place-content: center;
  place-items: center;
  gap: 10px;
  min-width: 700px;
  padding: 10px;
  display: grid;
}

header.dark-mode {
  color: #e0e0e0;
  background-color: #16213e;
}

.iniciar_sesion {
  grid-column: 10;
  justify-content: end;
  justify-items: center;
}

.titulo.dark-mode {
  color: #e0e0e0;
}

.titulo {
  font-size: 50px;
}

html, body {
  background-color: #bdd8f1;
  flex-direction: column;
  min-width: fit-content;
  min-height: 100vh;
  margin: 0;
  display: flex;
}

body.dark-mode, html.dark-mode {
  color: #e0e0e0;
  background-color: #0f3460;
}

button {
  color: #fff;
  border: none;
  width: 100%;
  font-size: 24px;
}

button.modo {
  cursor: pointer;
  color: #e0e0e0;
  background-color: #3667a6;
  grid-area: 1 / 12 / 2 / 13;
}

button.dark-mode {
  background-color: #0f3460;
}

a.dark-mode {
  color: #e0e0e0;
}

h1 {
  unicode-bidi: isolate;
  margin-block: auto;
  margin-inline: auto;
  font-weight: normal;
}

a {
  color: #000;
  font-size: 32px;
  text-decoration: none;
}

p {
  color: #000;
  margin: 0;
  font-size: 32px;
  text-decoration: none;
}

main {
  grid-template-columns: repeat(auto-fill, 550px);
  justify-content: center;
  gap: 10px;
  min-height: 100%;
  margin-bottom: 5%;
  padding: 20px;
  display: grid;
}

section {
  border-radius: 30px;
  grid-template-rows: repeat(5, 1fr);
  min-width: 85%;
  height: 350px;
  margin-left: 10px;
  margin-right: 10px;
  display: grid;
}

h2.nombre {
  unicode-bidi: isolate;
  color: #fff;
  text-align: center;
  background-color: #000;
  border-bottom-right-radius: 30px;
  border-bottom-left-radius: 30px;
  grid-area: 5 / 1 / 6 / 2;
  margin: 0;
  font-size: 64px;
  font-weight: normal;
}

section.lugar1 {
  background: url("Medina-Sidonia.5814bbdf.png") center / cover no-repeat;
}

